.bg-primary {
    background-color: #fa5553 !important;
}

.card {
    border: 0px;
}

.subbtn {
    background-color: #f0c066;
    border-color: #f0c066;
    color: white;
}

.container {
    margin-top: 18px;
    padding: 0;
}

.Collapsible {
    width: 300px;
    margin: 10px;
}

.Collapsible__trigger {
    font-size: 32px;
    display: block;
    background-color: #78a3d7;
    color: white;
}

.Collapsible__contentInner {
    margin-top: 10px;
    margin-bottom: 8px;
    text-decoration: none;
}


.row {
    margin: none;
}

div {
-ms-flex-pack: center;
    justify-content: center;
padding: 0;
text-decoration: none;
}

@media (min-width: 576px) {
.col-sm-2 {
   max-width: none;

}
}


